.dark.vista-container {
    background-color: #a6a19e;
    padding-bottom: 10px
}

.vista-container {
    width: 100%;
    height: 100%;
    margin: 5px;
    padding: 1px;
}
.tab-container  {
    display: flex;
    margin-top: 24px;
    margin-bottom: 12px;
}
.tabs {
    width: 15%;
    padding: 6px 24px;
    border-bottom: 1px solid lightgrey;
    text-align: center;
}
@media screen and (max-width: 1024px) {
    .tabs {
        width: 50%;
    }
}
.tabs.second {
    border: 1px solid lightgrey;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border-bottom: none;
    box-shadow: -3px -2px 3px #d5dbdb;
}
.tabs.first {
    border: 1px solid lightgrey;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    border-bottom: none;
    box-shadow: 3px -2px 3px #d5dbdb;
}
.table-container, .config-container {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-flow: wrap;
}
.config-container > div:last-child {
    border-right: 2px solid red;
    padding-right: 15px;
}
.config-container > div {
    margin: 5px;
    border-left: 2px solid red;
    padding-left: 15px;
}
.tab-space {
    margin-left: 16px;
}
.code-block {
    margin-left: 8px;
}
.code-container {
    margin-top: 10px;
    background-color: lightgrey;
    border-radius: 5px;
    margin: 10px;
    max-height: 250px;
    overflow-y: auto;
}

.table-container::-webkit-scrollbar, .code-container::-webkit-scrollbar {
    width: 8px;
}

.table-container::-webkit-scrollbar-track, .code-container::-webkit-scrollbar-track {
    background-color: #e4e4e4;
    border-radius: 5px;
}

.table-container::-webkit-scrollbar-thumb, .code-container::-webkit-scrollbar-thumb {
    background-color: grey;
}

.select-style {
    height: 25px;
    padding: 2px;
    border-radius: 5px;
    border: 2px solid grey;
    border-bottom-color: #7a716e;
    border-right-color: #7a716e;
    border-top-color: #bcb8b6;
    border-left-color: #bcb8b6;
}

/* The container */
.container {
    display: block;
    position: relative;
    padding-left: 32px;
    cursor: pointer;
    font-size: 14px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    margin-top: 2px;
    font-size: medium;
}

/* Hide the browser's default checkbox */
.container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 16px;
    width: 16px;
    border-radius: 3px;
    background-color: white;
    border: 2px solid grey;
    border-bottom-color: #7a716e;
    border-right-color: #7a716e;
    border-top-color: #bcb8b6;
    border-left-color: #bcb8b6;
}

/* On mouse-over, add a grey background color */
.container:hover input.checkmark {
    background-color: #645a56;
}

/* When the checkbox is checked, add a blue background */
.container input:checked.checkmark {
    background-color: #645a56;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.container input:checked~.checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
    left: 5px;
    top: 1px;
    width: 3px;
    height: 8px;
    border: solid #645a56;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}