.top-navbar {
    height: 2em;
    padding: 1% 1%;
    background: linear-gradient(135deg, #ffffff 15%,#f6f6f6 26%,#f6f6f6 26%,#f1f1f1 100%,#e8e8e8 100%);
    border-bottom: 1px solid rgba(31, 45, 61, .15);
    -webkit-filter: drop-shadow( 2px 1px 2px rgba(0, 0, 0, .7));
    filter: drop-shadow( 1px 1px 1px rgba(0, 0, 0, .7));
}
.logo-text {
    font-size:30px;
    color:blue;
    font-weight:bolder;
    font-size:25px;
    margin-top: 4px;
    margin-left: 4px;
}
.logo-symbols {
    font-style: italic;
    font-size:30px;
    font-weight:bolder;
}
.avatar {
    border-radius: 15px;
    background: #fff;
    width: 2rem;
    height: 2rem;
    margin: 1px;
    transition: 0.5s;
}
.display-flex {
    display: flex;
}
.display-grid {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 10px;
}
.float-right {
    float: right;
}
