.double-quotes {
    font-size: 40px;
    color: rgb(241, 83, 83);
    display: inline-block;
}
.quotes {
    display: inline-block;
    font-size: 18px;
    margin-top: 12px;
    margin-left: 5px;
}
.quotes-container {
    display: flex;
}
.floating-badge {
    position: fixed;
    top: 30%;
    height: fit-content;
    background-color: rgb(248, 193, 193);
    right: 0;
    padding: 4px;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    box-shadow: -2px -2px 3px #d5dbdb;
}
.floating-badge a {
    text-decoration: none;
}
.floating-badge a span{
    
    display: block;
    margin-left: 4px;
    margin-right: 4px;
}